<template xmlns:v-slot="http://www.w3.org/1999/XSL/Transform">

    <v-tabs v-model="active" color="cyan" dark slider-color="yellow">
        <v-tab v-for="p in trip" :key="p.id" ripple>
            {{p}}
        </v-tab>
        <v-timeline>
            <v-timeline-item v-for="n in trip" :key="n.id" color="black lighten-2" small>
                <template v-slot:opposite>
                    <span>{{n.name}}</span>
                </template>
                <CardItem :card="n" />
            </v-timeline-item>
        </v-timeline>
    </v-tabs>

</template>

<script>
    import CardItem from './CardItem';
    import store from '../store';

    export default {
        name: "Timeline",
        components: {CardItem},
        data(){
            return{}
        },
        computed: {
            trip(){
                return store.state.currentViewedTrip.places.filter(place => {
                    if (place.suggested === false){
                        return place;
                    }
                })
            },
            dates(){
                return store.state.currentViewedTrip.start_date.split('-')[2]
            }
        }
    }
</script>

